import socket from '../CLASSES/Socket';
import codes from '../CLASSES/opcode';

class HomeInfoResponse {
    /** @param {[Number,Number,Number]} response */
    constructor(response = []) {
        if (!Array.isArray(response)) response = [0, 0, 0];
        this.players = response[0];
        this.online = response[1];
        this.servers = response[2];
    }
}
/** @returns {Promise<HomeInfoResponse>} */
function requestHome() { return socket.request(codes.HOME_PAGE); }
socket.defineStructure(codes.HOME_PAGE, HomeInfoResponse);

export default requestHome;